import React from 'react';
import { Box, Button, Typography, Container } from '@mui/material';
import PhoneIcon from '@mui/icons-material/Phone';
import { useAppDispatch } from 'src/assets/hooks';
import { setPopup } from 'src/store/Slices/popupSlice';

interface ConfusedBannerProps {
  title?: string;
  description?: string;
  buttonText?: string;
  isSmall?: boolean;
  onButtonClick?: () => void;
}

/**
 * Reusable HOC Component for "Confused?" / "Let us simplify it" banner sections
 * Can be used across different pages with customizable content
 */
const ConfusedBannerHOC: React.FC<ConfusedBannerProps> = ({
  title = "Confused?",
  description = "Let us simplify it for you, talk to one of our experts now for free!",
  buttonText = "Talk to expert",
  isSmall = false,
  onButtonClick,
}) => {
  const dispatch = useAppDispatch();

  const handleButtonClick = () => {
    if (onButtonClick) {
      onButtonClick();
    } else {
      // Default behavior - open callback popup
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "callback",
              dimensions: {
                width: "500px",
                height: { xs: "auto", md: "420px", lg: "410px" } as any,
              },
            },
            type: "custom",
          },
        })
      );
    }
  };

  return (
    <Box
      sx={{
        py: { xs: 4, md: 6 },
        px: { xs: 2, md: 4 },
        backgroundColor: "white",
      }}
    >
      <Container maxWidth="lg">
        <Box
          sx={{
            width: "100%",
            maxWidth: isSmall ? 800 : 1200,
            mx: "auto",
            my: 3,
          }}
        >
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "40px",
              background: "#70A6B2",
              position: "relative",
              px: isSmall ? { xs: 2, sm: 4 } : { xs: 3, sm: 6 },
              py: isSmall ? { xs: 3, sm: 4 } : { xs: 4, sm: 5 },
              minHeight: isSmall ? 100 : 120,
              textAlign: "center",
            }}
          >
            {/* Left circle cutout */}
            <Box
              sx={{
                position: "absolute",
                left: -20,
                top: "50%",
                transform: "translateY(-50%)",
                width: 40,
                height: 40,
                borderRadius: "50%",
                background: "white",
                zIndex: 1,
              }}
            />

            {/* Right circle cutout */}
            <Box
              sx={{
                position: "absolute",
                right: -20,
                top: "50%",
                transform: "translateY(-50%)",
                width: 40,
                height: 40,
                borderRadius: "50%",
                background: "white",
                zIndex: 1,
              }}
            />

            {/* Content Section - All Centered */}
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
                zIndex: 2,
                width: "100%",
                gap: { xs: 2, md: 3 },
              }}
            >
              {/* Text Section */}
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  textAlign: "center",
                  width: "100%",
                }}
              >
                <Typography
                  color="white"
                  sx={{
                    fontSize: isSmall
                      ? { xs: "1.5rem", md: "2rem" }
                      : { xs: "1.8rem", md: "2.5rem" },
                    fontWeight: 600,
                    mb: 1,
                  }}
                >
                  {title}
                </Typography>

                <Typography
                  color="rgba(255, 255, 255, 0.9)"
                  sx={{
                    fontSize: isSmall
                      ? { xs: "0.9rem", md: "1rem" }
                      : { xs: "1rem", md: "1.3rem" },
                    maxWidth: { xs: "100%", md: "800px" },
                    lineHeight: 1.4,
                  }}
                >
                  {description}
                </Typography>
              </Box>

              {/* Button Section */}
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Button
                  data-no-close="true"
                  onClick={(e) => {
                    e.stopPropagation();
                    e.preventDefault();
                    handleButtonClick();
                  }}
                  sx={{
                    fontSize: isSmall ? "12px" : "14px",
                    height: isSmall ? "36px" : "44px",
                    minWidth: isSmall ? "140px" : "160px",
                    borderRadius: "25px",
                    background: "#fff",
                    color: "#495052",
                    border: "2px solid #F48438",
                    fontWeight: 600,
                    boxShadow: "none",
                    display: "flex",
                    alignItems: "center",
                    gap: 1,
                    textTransform: "none",
                    "&:hover": {
                      background: "#F48438",
                      color: "#FFF",
                    },
                  }}
                >
                  {buttonText}
                  <PhoneIcon sx={{ fontSize: isSmall ? 14 : 16 }} />
                </Button>
              </Box>
            </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default ConfusedBannerHOC;

